  //fastlistbox 一级名单下拉框
  fastlistbox() {
    this.parentListTypeId="",
    this.listTypeId=''
    const pams = {
      listTypeHierarchy: 1,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.fastlistbz=[{ label: "请选择", value: "" }]
        
        data.returnList.forEach(item => {
          this.fastlistbz.push({ label: item.listTypeName, value: item.listTypeId });
        });
        console.log(this.fastlistbz[0].value)
        //this.nextlistbox(this.fastlistbz[0].value)
      }
    })
  }
  //nextlistbox 二级名单下拉框
  nextlistbox(s) {
    this.parentListTypeId="",
    this.listTypeId=''
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    this.nextlistbz = [{ label: "请选择", value: "" }];
    const pams = {
      parentListTypeId: s,
      listTypeHierarchy: 2,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnList.forEach(item => {
          this.nextlistbz.push({ label: item.listTypeName, value: item.listTypeId });
        });
      }
    })
  }
  nextlistbzsd(s) {
    this.listTypeId=''
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    const pams = {
      parentListTypeId: s,
      listTypeHierarchy: 3,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnList.forEach(item => {
          this.nextlistbzs.push({ label: item.listTypeName, value: item.listTypeId });
        });
      }
    })
  }
  // 一级维护
  fastmanage() {
    this.headerTitle = '一级名单维护';
    this.display1 = true;
    // this.fastlist = false;
    const pams = {
      listTypeHierarchy: 1,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData1 = data.returnList ? data.returnList : [];
        this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // 二级维护
  lastmanage() {
    this.headerTitle = '二级名单维护';
    this.display2 = true;
    // this.lastlist = false;
    const pams = {
      listTypeHierarchy: 2,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData2 = data.returnList ? data.returnList : [];
        this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //fastlistbox 一级名单下拉框
    this.fastlistbox()
  }
  // 一级新增
  add1() {
    this.isadd1 = true;
    this.typeId = null;
    this.listTypeName = null;
    this.comments = null;
    this.listTypeId = null;
    this.typeId = null;
    // this.FastListBen = new FastListBen();
    this.fastlist = true;
  }
  // 二级新增
  add2() {
    this.isadd2 = true;
    // this.nextListBen = new NextListBen();
    this.typeIds = null;
    this.listTypeNames = null;
    this.commentss = null;
    this.listTypeIds = null;
    this.parentListTypeIds = null;
    this.typeIds = null;
    this.lastlist = true;
  }

  // 返回关闭
  back() {
    this.dismus3 = false;
  }
  back1() {
    this.fastlist = false;
  }
  back2() {
    this.lastlist = false;
  }
  //重置
  resetClick() {
    
    this.fastlistbzs = null;
    this.parentListTypeId = null;
    // this.riskRange = null;
    this.listTypeId = null;
    this.nextlistbz = [{ label: "请选择", value: "" }];
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    this.pageSize = 10;
    this.pageNum = 1;
    this.first = 1;
    this.queryClick(2);
  }
  downkey: boolean = false;
  // 下载
  downlon() {
    this.downkey = true;
    this.headerTitle1 = "下载提示";
    const pams = {
      listTypeId: this.listTypeId,
      parentListTypeId: this.parentListTypeId,
      riskRange: this.riskRange,
    }
    this.cusOperationHttpService.downLoadListTypeInfo(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.fileName = data.fileName;
        this.fileUrl = data.fileUrl;
        this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.config();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.downkey = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      this.downkey = false;
    })
  }
  // 下载弹框
  config() {
    this.confirmationService.confirm({
      message: "是否要下载",
      accept: () => {
        this.downkey = false;
        if (this.fileName) {
          open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.fileUrl, '_blank');
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '所要下载文件不存在' });
        }
      },
      reject: () => {
        this.downkey = false;
      }
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
  }
  modifyCall(e) {
    this.dismus4 = e.display;
    this.queryClick(1)
  }
  closeSetting() {
    //调用查询的方法
    if (this.fastlistbzs || this.parentListTypeId || this.listTypeId) {
      this.queryClick(1);
      //alert(11111111)
    } else {
      this.resetClick()
      //alert(222222222)
    }

  }
  closeSetting2(){
    this.resetClick()
  }
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
