import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/primeng";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";

@Component({
    selector: 'custom-manage-batch',
    templateUrl: './custom-manage-batch.component.html',
    styleUrls: ['./custom-manage-batch.component.scss'],
})
export class CustomManageBatch implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    loadingShow: any = { flag: false }; // loading框
    msgs: Message[] = [];
    userform: FormGroup;
    isMybankCustList: any;//是否我行客户
    customerTypeList: any;//客户属性
    statusIdList: any;//状态
    inListTypeList: any;//录入方式
    listTypeOneList: any;//一级名单
    listTypeTwoList: any;//二级名单
    code: any;
    inStock: boolean = false;//入库
    outStock: boolean = false;//出库
    // 文件
    files: any = [];
    fileList: any = [];
    UPLOAD: string = TZB_CUS_UPLOAD;// 上传
    user: any;//用户信息
    today = new Date();//日期
    jbxx: any = [];//名单码值
    userName: any;//当前登陆人
    orgName: any;//登陆人所属机构
    Ashplacing: boolean = false;
    fileSuccess:boolean = false;    // 文件是否上传
    constructor(
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.userName = chName.userName
        this.orgName = chName.orgName
        this.userform = fb.group({
            listTypeOne: ['', Validators.required],//一级名单
            sourceDes: ['', Validators.required],//名单来源
            inListType: ['2', Validators.required],//录入方式
            toListReason: [''],//录入原因
            registerUser: [''],//经办人
            registerOrg: [''],//经办机构
            registerTime: [''],//经办时间
            filePath: [''],//文件路径
            fileName: [''],//文件名称
            registerUserName: [''],//登陆人
            registerOrgName: [""],//登陆机构
        });
    }
    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.codeValues() //调用方法，获取全部码值
        this.isMybankCustList = this.code['isMybankCust'];
        this.customerTypeList = this.code['customerType'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        if (this.inValue == '1') {//入库页面
            this.inStock = true;
            this.outStock = false;
        } else if (this.inValue == '2') {//出库页面
            this.inStock = false;
            this.outStock = true;
            this.userform.removeControl('sourceDes');
            this.userform.removeControl('inListType');
        }
        this.userform.get('registerUser').setValue(this.user.userId);
        this.userform.get('registerUserName').setValue(this.userName);
        this.userform.get('registerOrg').setValue(this.user.extParam.orgId);
        this.userform.get('registerOrgName').setValue(this.orgName);
        this.userform.get('registerTime').setValue(this.commfunc.transDateN(this.today));
        this.query();
        this.queryOrgName()
    }
    queryOrgName() {
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        for (var i = 0; i < chName.orgList.length; i++) {
            if (this.user.extParam.orgId == chName.orgList[i].orgId) {
                this.orgName = chName.orgList[i].orgName
            }
        }
        console.log(this.orgName, "111111111")
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        const param = {};
        param['listTypeHierarchy'] = 1;
        this.httpService.selectBlackListType(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let list = data.returnList;
                list.forEach(element => {
                    let obj = {};
                    obj['label'] = element.listTypeName;
                    obj['value'] = element.listTypeId;
                    this.jbxx.push(obj);
                });
                this.jbxx.unshift({ label: '请选择', value: '' });
                this.listTypeOneList = this.jbxx;
                this.msgs = [];
                // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 按模板-文件上传
    load(event) {
        for (let file of event.files) {
            this.files = [];
            this.files.push(file)
        }
        if (event.xhr.response) {
            this.fileList = JSON.parse(event.xhr.response);
            if(this.fileList.length > 0){
                this.fileSuccess = true;
            }else{
                this.fileSuccess = false;
            }
            if (this.fileList.success == false) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                this.files = [];
                return
            }
        }
        if (this.fileList[0]) {
            this.userform.get('filePath').setValue(this.fileList[0].fileUrl);
            this.userform.get('fileName').setValue(this.fileList[0].fileName);
        }
    }
    // 保存/提交
    doSave(i) {
        if (this.inValue == '1') {//入库
            if (i == '1') {//入库保存
                const param = Object.assign({}, this.userform.value);
                param['operationType'] = '1002';
                param['approvalStatus'] = 'AA';
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                this.Ashplacing = true;
                this.httpService.superListBatchOperation(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        // this.outValue.emit(false);
                        // this.msgs = [];
                        //  this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        setTimeout(() => {
                            this.outValue.emit(false)
                        }, 1000)
                    } else {
                        this.Ashplacing = false;
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.Ashplacing = false;                    
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
            if (i == '2') {//入库提交
                const param = Object.assign({}, this.userform.value);
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                param['operationType'] = '1002';
                param['approvalStatus'] = '0';
                this.Ashplacing = true;                
                this.httpService.superListBatchOperation(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        // this.outValue.emit(false);
                        // this.msgs = [];
                        // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        setTimeout(() => {
                            this.outValue.emit(false)
                        }, 1000)
                    } else {
                        this.Ashplacing = false;                        
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.Ashplacing = false;                    
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        } else if (this.inValue == '2') {//出库
            if (i == '1') {//出库保存
                const param = Object.assign({}, this.userform.value);
                param['operationType'] = '1004';
                param['approvalStatus'] = 'AA';
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                this.Ashplacing = true;                
                this.httpService.superListBatchOperation(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        // this.outValue.emit(false);
                        // this.msgs = [];
                        // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        setTimeout(() => {
                            this.outValue.emit(false)
                        }, 1000)
                    } else {
                        this.Ashplacing = false;                        
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.Ashplacing = false;                    
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
            if (i == '2') {//出库提交
                const param = Object.assign({}, this.userform.value);
                param['operationType'] = '1004';
                param['approvalStatus'] = '0';
                param['registerOrg'] = this.user.extParam.orgId;
                param['registerUser'] = this.user.userId;
                param['registerUserName'] = this.userName
                param['registerOrgName'] = this.orgName
                this.Ashplacing = true;                
                this.httpService.superListBatchOperation(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        // this.outValue.emit(false);
                        // this.msgs = [];
                        // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                        setTimeout(() => {
                            this.outValue.emit(false)
                        }, 1000)
                    } else {
                        this.Ashplacing = false;                        
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.Ashplacing = false;                    
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        }
    }
    // 返回
    doReset() {
        this.outValue.emit(false);
    }
    // 模板下载
    downword() {
        if (this.inValue == '1') {
            open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI("listManagerInImport.xls") + "&fileUrl=" + "/copdata/moban/", '_blank');
        } else if (this.inValue == '2') {
            open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI("listManagerOutImport.xls") + "&fileUrl=" + "/copdata/moban/", '_blank');
        }
    }
}