import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { LogBean } from './bean/custom-list-log.bean';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
@Component({
  selector: 'app-custom-list-log',
  templateUrl: './custom-list-log.component.html',
  styleUrls: ['./custom-list-log.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CustomListLogComponent implements OnInit {
  pageNum: number;
  //表数据
  tableData: any = [];
  operationTypes = [
    { value: "", label: "请选择" },
    { value: "1001", label: "名单入库" },
    { value: "1002", label: "名单批量入库" },
    { value: "1003", label: "名单出库" },
    { value: "1004", label: "名单批量出库" },
    { value: "1005", label: "名单等级调整" },
    { value: "1006", label: "名单失效" },
    { value: "1007", label: "名单查询" },
    { value: "1008", label: "名单分析" },
    { value: "1009", label: "名单分析下载" },
    { value: "1010", label: "名单查询日志下载" },
  ];

  LogBean: LogBean = new LogBean();
  total: string; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  dataDate: Date = new Date();
  fileName: any;
  fileUrl: any;
  headerTitle: any;
  display3 = false;
  personPage: boolean = false;
  queryStartDate: Date = null;
  queryEndDate: Date = null;
  //校验
  code: any;
  pageSize = 10;
  pageNumber = 1;
  userInValue: any;
  newdateTime: Date = new Date();
  constructor(
    public cusOperationHttpService: CusListManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public router: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值

  }

  ngOnInit() {
    this.pageSize = 10;
    this.pageNum = 1;
    this.first = 1;
   //this.queryClick();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  downkey: boolean = false;
  downlon() {
    this.downkey = true;
    this.LogBean.registerTimeStart = null;
    this.LogBean.registerTimeEnd = null;
    if (this.queryStartDate) {
      this.LogBean.registerTimeStart = this.commfunc.transDateN(this.queryStartDate);
    }
    if (this.queryEndDate) {
      this.LogBean.registerTimeEnd = this.commfunc.transDateN(this.queryEndDate);
    }
    if (!this.queryEndDate) { //如果没有输入最后录入时间 默认最后日期为当前
      this.LogBean.registerTimeEnd = this.commfunc.transDateN(this.dataDate);
    }
    this.cusOperationHttpService.selectDownListQueryLog(this.LogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.fileName = data.fileName;
        this.fileUrl = data.fileUrl;
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        // this.config()
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }
  config() {
    this.headerTitle = "下载提示";
    this.confirmationService.confirm({
      message: "是否下载操作日志？",
      accept: () => {
        if (this.fileName) {
          open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.fileUrl, '_blank');
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '所要下载文件不存在' });
        }
      },
      reject: () => { }
    });
  }
  //查询
  queryClick() {
    //查询时间
    this.LogBean.pageSize = this.pageSize;
    this.LogBean.pageNum = this.pageNum;
    this.LogBean.registerTimeStart = null;
    this.LogBean.registerTimeEnd = null;
    if (this.queryStartDate) {
      this.LogBean.registerTimeStart = this.commfunc.transDateN(this.queryStartDate);
    }
    if (this.queryEndDate) {
      this.LogBean.registerTimeEnd = this.commfunc.transDateN(this.queryEndDate);
    }
    if (!this.queryEndDate) { //如果没有输入最后录入时间 默认最后日期为当前
      this.LogBean.registerTimeEnd = this.commfunc.transDateN(this.dataDate);
    }

    this.cusOperationHttpService.selectListQueryLog(this.LogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList;
        this.total = data.total;
        this.msgs = [];
       // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  chooseWorker() {
    this.userInValue = '0000';
    this.personPage = !this.personPage;
  }
  perInfor(item) {
    this.personPage = false;
    if (item.length > 0) {
      this.LogBean.registerUser = item[0].tellerId;
      this.LogBean.registerUserName = item[0].tellerName;
    }
  }
  //关闭模态框
  onHideClose() {
    this.personPage = false;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.queryClick();
  }

  //重置
  resetClick() {
    this.queryStartDate = null;
    this.queryEndDate = null;
    this.pageSize = 10;
    this.pageNum = 1;
    this.LogBean = new LogBean();
  }
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
}
  // //日期范围的处理
  // selectTime1() {
  //   this.selectTime(1);
  // }
  // // selectTime2() {
  // //   this.queryStartDate.getHours() == new Date().getHours() ? this.selectTime(2) : this.selectTime(1);
  // // }
  // selectTime(a) {
  //   if (this.queryStartDate && this.queryEndDate) {
  //     if (this.queryStartDate > this.queryEndDate) {
  //       this.msgs = [];
  //       this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
  //       setTimeout(() => {
  //         this.queryStartDate = null;
  //         this.queryEndDate = null;
  //       })
  //     }
  //   }
  // }


}