import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { AnayBean } from './bean/custom-list-analy.bean';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
@Component({
  selector: 'app-custom-list-analy',
  templateUrl: './custom-list-analy.component.html',
  styleUrls: ['./custom-list-analy.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CustomListAnalyComponent implements OnInit {
  userDisplay: any;
  pageNum: number;
  //表数据
  tableData: any = [];
  loadingShow: any = { flag: false }; // loading框
  statusIds = [
    { label: "请选择", value: "" },
    { label: "有效", value: "Y" },
    { label: "无效", value: "N" },
  ];
  fastlistbz = [{ label: "请选择", value: "" }];
  nextlistbz = [{ label: "请选择", value: "" }];
  nextlistbzs = [{ label: "请选择", value: "" }];
  //分页
  total: string; //总页数
  first: number = 0;
  totalDaySum: number = 0;
  totalMonthSum: number = 0;
  totalSum: number = 0;
  ownerOrgInValue: any;
  orgTreeDisplay = false;
  personPage: boolean = false;
  //提示信息
  msgs: Message[] = [];
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  queryStartDate: Date = null;
  queryEndDate: Date = null;
  dateTime: Date = new Date();
  //校验
  userform: FormGroup;
  code: any;
  pageSize = 10;
  pageNumber = 1;
  AnayBean: AnayBean = new AnayBean();
  idenTypes = [];
  user: any;//用户信息
  chName: any;//用户信息
  newdateTime: Date = new Date();
  fileName: any;
  fileUrl: any;
  headerTitle: any;
  inListTypeList:any;
  constructor(
    public cusOperationHttpService: CusListManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public router: Router,
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.codeValues() //调用方法，获取全部码值
    this.inListTypeList = this.code['inListType'];
  }

  ngOnInit() {
    // this.AnayBean.registerOrg = this.user.extParam.orgId;
    // this.AnayBean.registerOrgName = this.chName.orgName;
    this.idenTypes = this.code['ALL_IDEN_TYPE'];
    // this.AnayBean.statusId = 'Y';
    this.fastlistbox();
    // this.queryClick();
    setTimeout(() => {
      this.tableData = [];
    })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; //code是自己声明的变量

  }
  openOrg(param) {
    this.orgTreeDisplay = true;
    // this.ownerOrgInValue = 'noShow'
  }
  onHideClose() {
    this.personPage = false;
    this.orgTreeDisplay = false;
  }
  treeCall(param) {
    this.orgTreeDisplay = false;
    this.AnayBean.registerOrgName = param.orgName;
    this.AnayBean.registerOrg = param.orgId;
  }
  fastlistbox() {
    this.nextlistbz = [{ label: "请选择", value: "" }];
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    const pams = {
      listTypeHierarchy: 1,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnList.forEach(item => {
          this.fastlistbz.push({ label: item.listTypeName, value: item.listTypeId });
        });
      }
    })
  }
  //nextlistbox 二级名单下拉框
  nextlistbox(e) {
    this.AnayBean.listTypeTwo = null;
    this.AnayBean.listTypeId = null;
    this.nextlistbz = [{ label: "请选择", value: "" }];
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    const pams = {
      parentListTypeId: e.value,
      listTypeHierarchy: 2,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnList.forEach(item => {
          this.nextlistbz.push({ label: item.listTypeName, value: item.listTypeId });
        });
      }
    })
  }
  //nextlistboxs 三级级名单下拉框
  nextlistboxs(t) {
    this.AnayBean.listTypeId = null;
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    const pams = {
      parentListTypeId: t.value,
      listTypeHierarchy: 3,
    };
    this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnList.forEach(item => {
          this.nextlistbzs.push({ label: item.listTypeName, value: item.listTypeId });
        });
      }
    })
  }


  //查询
  queryClick() {
    this.loadingShow.flag = true;
    //查询时间
    this.AnayBean.pageSize = this.pageSize;
    this.AnayBean.pageNum = this.pageNum;
    this.AnayBean.operationType = '1008';
    this.AnayBean.registerUserName = this.chName.userName;
    this.AnayBean.registerTimeStart = null;
    this.AnayBean.registerTimeEnd = null;
    if (this.queryStartDate) {
      this.AnayBean.registerTimeStart = this.commfunc.transDateN(this.queryStartDate);
    }
    if (this.queryStartDate) {
      this.AnayBean.registerTimeEnd = this.commfunc.transDateN(this.queryEndDate);
    }
    if (!this.queryEndDate) { //如果没有输入最后录入时间 默认最后日期为当前
      this.AnayBean.registerTimeEnd = this.commfunc.transDateN(this.dateTime);
    }

    this.cusOperationHttpService.selectBlackListInfoAnalysis(this.AnayBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.totalDaySum = data.totalDaySum;
        this.totalMonthSum = data.totalMonthSum;
        this.totalSum = data.totalSum;
        this.total = data.total;
        this.tableData = data.returnList;
        this.loadingShow.flag = false;
        this.msgs = [];
        //  this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.loadingShow.flag = false;
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.loadingShow.flag = false;
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }



  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.queryClick();
  }

  //重置
  resetClick() {
    this.queryStartDate = null;
    this.queryEndDate = null;
    this.dateTime = new Date();
    this.pageSize = 10;
    this.pageNum = 1;
    this.AnayBean = new AnayBean();
    this.AnayBean.statusId = '';
    this.fastlistbz = [{ label: "请选择", value: "" }];
    this.nextlistbz = [{ label: "请选择", value: "" }];
    this.nextlistbzs = [{ label: "请选择", value: "" }];
    this.fastlistbox()
    //调用查询的方法
    this.queryClick();
  }
  // 选择主联系人
  openPeople() {
    this.userDisplay = true;
  }
  workerCall(e) {
    this.userDisplay = false;
    this.AnayBean.mainUserName = e[0].tellerName;
    this.AnayBean.mainUser = e[0].tellerId;
  }
  // 文件下崽
  downlon() {
    this.AnayBean.operationType = '1009';
    this.AnayBean.registerUserName = this.chName.userName;
    this.AnayBean.registerTimeStart = null;
    this.AnayBean.registerTimeEnd = null;
    this.AnayBean.pageSize = null;
    this.AnayBean.pageNum = null;
    if (this.queryStartDate) {
      this.AnayBean.registerTimeStart = this.commfunc.transDateN(this.queryStartDate);
    }
    if (this.queryEndDate) {
      this.AnayBean.registerTimeEnd = this.commfunc.transDateN(this.queryEndDate);
    }
    if (!this.queryEndDate) { //如果没有输入最后录入时间 默认最后日期为当前
      this.AnayBean.registerTimeEnd = this.commfunc.transDateN(this.newdateTime);
    }
    this.cusOperationHttpService.selectDownListAnalysis(this.AnayBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];        
        this.fileName = data.fileName;
        this.fileUrl = data.fileUrl;
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });

        // this.headerTitle = "下载提示";
        // this.confirmationService.confirm({
        //   message: "是否下载统计分析文件？",
        //   accept: () => {
        //     if (this.fileName) {
        //       open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.fileUrl, '_blank');
        //     } else {
        //       this.msgs = [];
        //       this.msgs.push({ severity: 'error', summary: '提示', detail: '所要下载文件不存在' });
        //     }
        //   },
        //   reject: () => { }
        // });

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}