import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Message } from "primeng/primeng";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'custom-manage-detail',
    templateUrl: './custom-manage-detail.component.html',
    styleUrls: ['./custom-manage-detail.component.scss'],
})
export class CustomManageDetail implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    loadingShow: any = { flag: false }; // loading框
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    userform: FormGroup;
    isMybankCustList: any;//是否我行客户
    customerTypeList: any;//客户属性
    statusIdList: any;//状态
    inListTypeList: any;//录入方式
    listTypeOneList: any;//一级名单
    listTypeTwoList: any;//二级名单
    listTypeIdList: any;//风险等级
    idTypeList: any;//证件类型
    approvalStatusList: any;//审批状态
    operationTypeList: any;//操作类型
    code: any;
    adjust: boolean = false;//调整
    efficacy: boolean = false;//失效
    delete: boolean = false;//删除
    bank: boolean = false;//本行
    bankown: boolean = false;//非本行
    jbxx: any = [];//名单码值
    reList: any = [];//类型表
    dealPerShow: boolean = false;
    outVal: any;
    dataList: any = [];
    modifyValue: any;
    constructor(
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.userform = fb.group({
            isMybankCust: [''],//是否我行客户
            customerType: [''],//客户属性
            partyId: [''],//客户号
            custName: [''],//客户名称
            statusId: ['Y'],//状态
            listTypeOne: [''],//一级名单
            listTypeTwo: [''],//二级名单
            listTypeId: [''],//风险等级
            idType: [''],//证件类型
            idNo: [''],//证件号
            cellphone: [''],//联系号码
            contactAddress: [''],//联系地址
            inListType: ['1'],//录入方式
            classifyId: [''],//分类编码
            toListReason: [''],//录入原因
        })
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.isMybankCustList = this.code['isMybankCust'];
        this.customerTypeList = this.code['customerType'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        this.idTypeList = this.code['CERT_TYPE'];
        this.approvalStatusList = this.code['approvalStatus'];
        this.operationTypeList = this.code['operation'];
        if (this.inValue) {
            this.userform.get('isMybankCust').setValue(this.inValue.isMybankCust);
            this.userform.get('customerType').setValue(this.inValue.customerType);
            this.userform.get('partyId').setValue(this.inValue.partyId);
            this.userform.get('custName').setValue(this.inValue.custName);
            this.userform.get('statusId').setValue(this.inValue.statusId);
            this.userform.get('listTypeOne').setValue(this.inValue.listTypeOne);
            this.userform.get('listTypeTwo').setValue(this.inValue.listTypeTwo);
            this.userform.get('inListType').setValue(this.inValue.inListType);
            this.userform.get('classifyId').setValue(this.inValue.classifyId);
            this.userform.get('toListReason').setValue(this.inValue.toListReason);
            this.userform.get('listTypeId').setValue(this.inValue.listTypeId);
        }
        this.queryClick();
        this.isMybank();
        for (let e = 0; e < 3; e++) {
            this.listType(e);
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //是否我行客户
    isMybank() {
        if (this.inValue.isMybankCust == 'Y') {
            this.bank = true;
            this.bankown = false;
        } else if (this.inValue.isMybankCust == 'N') {
            this.bank = false;
            this.bankown = true;
            this.userform.get('idType').setValue(this.inValue.idType);
            this.userform.get('idNo').setValue(this.inValue.idNo);
            this.userform.get('cellphone').setValue(this.inValue.cellphone);
            this.userform.get('contactAddress').setValue(this.inValue.contactAddress);
        }
    }
    //名单联动
    listType(i) {
        if (i == '0') {
            const param = {};
            param['listTypeHierarchy'] = 1;
            // const configParam = {
            //     httpService: this.httpService,
            //     loadingFlag: true,
            //     messageFlag: true,
            //     inputParam: param,
            //     message: this.messageService,
            //     injectName: 'selectBlackListType',
            //     loadingShow: this.loadingShow,
            //     messageInfo: '查询成功',
            // }
            // this.commonFunc.haddleData(configParam, (bodyData) => {
            //     if (bodyData) {
                    // let list = bodyData.returnList;
                    // list.forEach(element => {
                    //     let obj = {};
                    //     obj['label'] = element.listTypeName;
                    //     obj['value'] = element.listTypeId;
                    //     this.jbxx.push(obj);
                    // });
                    // this.jbxx.unshift({ label: '请选择', value: '' });
                    // this.listTypeOneList = this.jbxx;
            //     }
            // });
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                   
           let list = data.returnList;
                    list.forEach(element => {
                        let obj = {};
                        obj['label'] = element.listTypeName;
                        obj['value'] = element.listTypeId;
                        this.jbxx.push(obj);
                    });
                    this.jbxx.unshift({ label: '请选择', value: '' });
                    this.listTypeOneList = this.jbxx;

                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else if (i == '1') {
            const param = {};
            param['listTypeHierarchy'] = 2;
            param['parentListTypeId'] = this.inValue.listTypeOne;
            // const configParam = {
            //     httpService: this.httpService,
            //     loadingFlag: true,
            //     messageFlag: true,
            //     inputParam: param,
            //     message: this.messageService,
            //     injectName: 'selectBlackListType',
            //     loadingShow: this.loadingShow,
            //     messageInfo: '查询成功',
            // }
            // this.commonFunc.haddleData(configParam, (bodyData) => {
            //     if (bodyData) {
            //         let list = bodyData.returnList;
            //         this.jbxx = [];
            //         list.forEach(element => {
            //             let obj = {};
            //             obj['label'] = element.listTypeName;
            //             obj['value'] = element.listTypeId;
            //             this.jbxx.push(obj);
            //         });
            //         this.jbxx.unshift({ label: '请选择', value: '' });
            //         this.listTypeTwoList = this.jbxx;
            //     }
            // });
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                   
         let list = data.returnList;
                    this.jbxx = [];
                    list.forEach(element => {
                        let obj = {};
                        obj['label'] = element.listTypeName;
                        obj['value'] = element.listTypeId;
                        this.jbxx.push(obj);
                    });
                    this.jbxx.unshift({ label: '请选择', value: '' });
                    this.listTypeTwoList = this.jbxx;

                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else if (i == '2') {
            const param = {};
            param['listTypeHierarchy'] = 3;
            param['parentListTypeId'] = this.inValue.listTypeTwo;
            // const configParam = {
            //     httpService: this.httpService,
            //     loadingFlag: true,
            //     messageFlag: true,
            //     inputParam: param,
            //     message: this.messageService,
            //     injectName: 'selectBlackListType',
            //     loadingShow: this.loadingShow,
            //     messageInfo: '查询成功',
            // }
            // this.commonFunc.haddleData(configParam, (bodyData) => {
            //     if (bodyData) {
            //         this.reList = bodyData.returnList;
            //         let list = bodyData.returnList;
            //         this.jbxx = [];
            //         list.forEach(element => {
            //             let obj = {};
            //             obj['label'] = element.listTypeName;
            //             obj['value'] = element.listTypeId;
            //             this.jbxx.push(obj);
            //         });
            //         this.jbxx.unshift({ label: '请选择', value: '' });
            //         this.listTypeIdList = this.jbxx;
            //     }
            // });
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                   
         this.reList = data.returnList;
                    let list = data.returnList;
                    this.jbxx = [];
                    list.forEach(element => {
                        let obj = {};
                        obj['label'] = element.listTypeName;
                        obj['value'] = element.listTypeId;
                        this.jbxx.push(obj);
                    });
                    this.jbxx.unshift({ label: '请选择', value: '' });
                    this.listTypeIdList = this.jbxx;

                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else if (i == '3') {
            for (let e = 0; e < this.reList.length; e++) {
                if (this.inValue.listTypeId == this.reList[e].listTypeId) {
                    this.userform.get('classifyId').setValue(this.reList[e].classifyId);
                }
            }
        }
    }
    //列表查询
    queryClick() {
        const param = { blackListId: this.inValue.blackListId };
        // const configParam = {
        //     httpService: this.httpService,
        //     loadingFlag: true,
        //     messageFlag: true,
        //     inputParam: param,
        //     message: this.messageService,
        //     injectName: 'selectListTaskInfo',
        //     loadingShow: this.loadingShow,
        //     messageInfo: '查询成功',
        // }
        // this.commonFunc.haddleData(configParam, (bodyData) => {
        //     if (bodyData) {
        //         this.dataList = bodyData.returnList ? bodyData.returnList : [];
        //     } else {
        //         this.dataList = [];
        //     }
        // });
        this.httpService.selectListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
               
                this.dataList = data.returnList ? data.returnList : [];

            } else {
                this.msgs = [];
                this.dataList = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    // 编号事件
    Jump(data) {
        this.modifyValue = { listTaskId: data.listTaskId, taskFlowId: data.taskFlowId };
        this.inValue = '';
        this.dealPerShow = true;
    }
    // 编号事件回调
    detailCall() {
        this.dealPerShow = false;
        // this.outValue.emit(false);
    }
    // 返回
    doReset() {
        this.outValue.emit(false);
    }
}