import { Component, OnInit,ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber, checkCard } from "app/@uisftech/common/service/validator";
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { Message } from "primeng/primeng";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Dialog } from "primeng/components/dialog/dialog";
@Component({
    selector: 'custom-manage',
    templateUrl: './custom-manage.component.html',
    styleUrls: ['./custom-manage.component.scss'],
})
export class customManage implements OnInit {
    @ViewChild('dialog') dialog: Dialog;        
    listTypeOne: any;
    listTypeTwo: any;
    listTypeId: any;
    discustom: boolean;
    msgs: Message[] = [];
    userform: FormGroup;
    loadingShow: any = { flag: false }; // loading框
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    first: number = 0;//分页控制
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    totalNum: number;//总条数
    dataList: any = [];
    riskList: any = [];
    idTypeList: any;//证件类型
    statusIdList: any;//状态
    inListTypeList: any;//录入方式
    display: boolean = false;//模态框
    riskdisplay: boolean = false;//模态框
    showModel: any;//模态框判断
    addUpHeader: any;//模态框判断表头
    inValue: any = [];//调整/失效/删除/批量出入库传参
    modifyValue: any;//调整/失效/删除判断
    cellphoneList: any = [];//号码列表
    fastlistbz = [{ label: "请选择", value: "" }];
    nextlistbz = [{ label: "请选择", value: "" }];
    nextlistbzs = [{ label: "请选择", value: "" }];
    tableHead: any[] = [{
        label: '客户号', value: 'partyId', wth: '100px'
    }, {
        label: '客户名称', value: 'custName', wth: '80px'
    }, {
        label: '证件号码', value: 'idNo', wth: '180px'
    }, {
        label: '一级名单', value: 'listTypeOneName', wth: '140px'
    }, {
        label: '二级名单', value: 'listTypeTwoName', wth: '140px'
    }, {
        label: '风险管控等级', value: 'listTypeName', wth: '120px'
    },
    // {
    //     label: '录入原因', value: 'toListReason', wth: '200px'
    // }, 
    {
        label: '生成日期', value: 'registerTime', wth: '120px'
    }, {
        label: '录入方式', value: 'inListType', wth: '80px'
    }, {
        label: '录入机构', value: 'registerOrgName', wth: '150px'
    }, {
        label: '主联系人', value: 'mainUserName', wth: '100px'
    }, {
        label: '主联系人机构', value: 'mainOrgName', wth: '120px'
    }, {
        label: '状态', value: 'statusId', wth: '50px'
    }];
    constructor(
        public cusOperationHttpService: CusListManageHttpService,
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.userform = fb.group({
            partyId: ['', [Validators.maxLength(10), checkNumber]],//客户号
            custName: ['', [Validators.maxLength(100), specialSymbol]],//客户名称
            // idNo: ['', [Validators.maxLength(32), checkNumber]],//证件号码非数校验
            idNo: ['', [Validators.maxLength(32), checkCard]],//证件号码身份证校验
        })
    }
    ngOnInit() {
        setTimeout(() => {
            this.dataList = [];
        })
        this.codeValues() //调用方法，获取全部码值
        this.idTypeList = this.code['CERT_TYPE'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        this.fastlistbox()
    }
    // key: boolean = false;//现行判断该客户号客户名称存不存在
    //客户号事件
    // partydetail() {
    //     if (this.userform.value.partyId) {
    //         const param = {};
    //         param['custNo'] = this.userform.value.partyId;
    //         this.httpService.selectCustBriefInfo(param).subscribe(data => {
    //             if (data.returnCode.code == 'success') {
    //                 this.userform.get('custName').setValue(data.custName);
    //             } else {
    //                 this.msgs = [];
    //                 this.msgs.push({ severity: 'error', summary: '提示', detail: '该客户号不存在!' });
    //             }
    //         }, err => {
    //             this.msgs = [];
    //             this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    //         });
    //     }
    // }

    fastlistbox() {
        this.nextlistbz = [{ label: "请选择", value: "" }];
        this.nextlistbzs = [{ label: "请选择", value: "" }];
        const pams = {
            listTypeHierarchy: 1,
        };
        this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.returnList.forEach(item => {
                    this.fastlistbz.push({ label: item.listTypeName, value: item.listTypeId });
                });
            }
        })
    }
    //nextlistbox 二级名单下拉框
    nextlistbox(e) {
        this.nextlistbz = [{ label: "请选择", value: "" }];
        this.nextlistbzs = [{ label: "请选择", value: "" }];
        const pams = {
            parentListTypeId: e.value,
            listTypeHierarchy: 2,
        };
        this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.returnList.forEach(item => {
                    this.nextlistbz.push({ label: item.listTypeName, value: item.listTypeId });
                });
            }
        })
    }
    //nextlistboxs 三级级名单下拉框
    nextlistboxs(t) {
        this.nextlistbzs = [{ label: "请选择", value: "" }];
        const pams = {
            parentListTypeId: t.value,
            listTypeHierarchy: 3,
        };
        this.cusOperationHttpService.selectBlackListType(pams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.returnList.forEach(item => {
                    this.nextlistbzs.push({ label: item.listTypeName, value: item.listTypeId });
                });
            }
        })
    }

    //查询
    query() {
        if (!this.userform.value.partyId && !this.userform.value.custName && !this.userform.value.idNo && !this.listTypeOne && !this.listTypeTwo && !this.listTypeId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入查询条件' });
            this.loadingShow.flag = false;
            return;
        } else if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo || this.listTypeOne || this.listTypeTwo || this.listTypeId) {
            this.pageSize = 10;
            this.pageNum = 1;
            this.first = 0;
            this.queryClick();
        }
    }
    //列表查询
    queryClick() {
        this.loadingShow.flag = true;
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['registerUserName'] = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
        param['listTypeOne'] = this.listTypeOne;
        param['listTypeTwo'] = this.listTypeTwo;
        param['listTypeId'] = this.listTypeId;
        param['operationType'] = '1007';
        this.httpService.selectBlackListInfoNew(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.returnList ? data.returnList : [];
                this.totalNum = data.total;
                this.msgs = [];
                // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.dataList = [];
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
            this.loadingShow.flag = false;
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            this.loadingShow.flag = false;
        });
    }
    // 新增
    add() {
        this.display = true;
        this.showModel = 1;
        this.addUpHeader = '入库申请';
    }
    // 新增回调
    addCall(param) {
        this.display = param;
        this.pageNum = 1;
        if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo || this.listTypeOne || this.listTypeTwo || this.listTypeId) {
            this.query()
        }

    }
    // 调整/失效/出库
    modify(col, i) {
        this.inValue = col;
        this.modifyValue = i;
        this.display = true;
        this.showModel = 2;
        if (i == '1') {
            this.addUpHeader = '风险管控等级调整';
        } else if (i == '2') {
            this.addUpHeader = '名单失效申请';
        } else if (i == '3') {
            this.addUpHeader = '出库申请';
        }
        if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo || this.listTypeOne || this.listTypeTwo || this.listTypeId) {
            this.query()
        }
    }
    // 调整/失效/出库回调
    updateCall(param) {
        this.display = param;
        this.pageNum = 1;
        if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo || this.listTypeOne || this.listTypeTwo || this.listTypeId) {
            this.query()
        }
    }
    // 详情
    detail(col, i) {
        this.display = true;
        this.inValue = col;
        this.showModel = 4;
        this.modifyValue = i;
        this.addUpHeader = '名单详情';
    }
    // 详情回调
    detailCall(param) {
        this.display = param;
    }
    //批量出入库
    batch(col) {
        this.inValue = col;
        this.display = true;
        this.showModel = 3;
        if (col == '1') {
            this.addUpHeader = '批量入库申请';
        } else if (col == '2') {
            this.addUpHeader = '批量出库申请';
        }
    }
    // 批量出入库回调
    batchCall(param) {
        this.display = param;
        this.pageNum = 1;
        if (this.userform.value.partyId || this.userform.value.custName || this.userform.value.idNo || this.listTypeOne || this.listTypeTwo || this.listTypeId) {
            this.query()
        }
    }
    //风险等级事件
    riskNumber(col) {
        this.riskdisplay = true;
        const param = {
            isMybankCust: col.isMybankCust,
            partyId: col.partyId,
            idNo: col.idNo,
            custName: col.custName,
            customerType: col.customerType,
            approvalStatus: '0',
            operationType: '1005',
        };
        this.httpService.selectListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.riskList = data.returnList ? data.returnList : [];
                this.msgs = [];
                //  this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.riskList = [];
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 重置
    reset() {
        this.userform.get('partyId').setValue(null);
        this.userform.get('custName').setValue(null);
        this.userform.get('idNo').setValue(null);
        this.listTypeOne = "";
        this.listTypeTwo = "";
        this.listTypeId = "";
        this.dataList = []
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        if (!this.userform.value.partyId && !this.userform.value.custName && !this.userform.value.idNo && !this.listTypeOne && !this.listTypeTwo && !this.listTypeId) {
            this.dataList = [];
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入查询条件' });
        } else {
            this.loadingShow.flag = true;
            this.queryClick();
        }
    }
    // 客户查询弹框
    perTreeShow() {
        this.discustom = true;
    }
    // 客户查询关闭传值
    getMsg(e) {
        this.userform.get('partyId').setValue(e.custNo);
        this.userform.get('custName').setValue(e.custName);
        this.discustom = false;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    cancelDialogShake() {
        if (this.dialog) {
            this.dialog.center = function () {
                let elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                let elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                if (elementWidth === 0 && elementHeight === 0) {
                    this.containerViewChild.nativeElement.style.visibility = 'hidden';
                    this.containerViewChild.nativeElement.style.display = 'block';
                    elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                    elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                    this.containerViewChild.nativeElement.style.display = 'none';
                    this.containerViewChild.nativeElement.style.visibility = 'visible';
                }
                let viewport = this.domHandler.getViewport();
                let x = Math.max(Math.ceil((viewport.width - elementWidth)) / 2, 0);
                let y = Math.max(Math.ceil((viewport.height - elementHeight)) / 2, 0);
                // 解决抖动问题
                if (!this.containerViewChild.nativeElement.style.left ||
                    !this.containerViewChild.nativeElement.style.top ||
                    Math.abs(this.containerViewChild.nativeElement.style.left - x) > 5 ||
                    Math.abs(this.containerViewChild.nativeElement.style.top - y) > 5
                ) {
                    this.containerViewChild.nativeElement.style.left = x + 'px';
                    this.containerViewChild.nativeElement.style.top = y + 'px';
                }
            };
        }

    }
}