
import { Component, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard, checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Dialog } from "primeng/components/dialog/dialog";

@Component({
    selector: 'custom-black-add',
    templateUrl: './custom-black-add.component.html',
    styleUrls: ['./custom-black-add.component.scss'],
})
export class CustomBlackAdd implements OnInit {
    cust: any;
    display: boolean = false;
    @Output() outValue = new EventEmitter();
    @Output() outValue2 = new EventEmitter();
    loadingShow: any = { flag: false }; // loading框
    userform: FormGroup;
    msgs: Message[] = [];
    code: any;
    isMybankCustList: any;//是否我行客户
    customerTypeList: any;//客户属性
    statusIdList: any;//状态
    inListTypeList: any;//录入方式
    listTypeOneList: any;//一级名单
    listTypeTwoList: any;//二级名单
    listTypeIdList: any;//风险等级
    idTypeList: any;//证件类型
    user: any;//用户信息
    today = new Date();//日期
    bank: boolean = false;//本行
    bankown: boolean = false;//非本行
    listTypeRisk: any;//名单联动参数
    jbxx: any = [];//名单码值
    reList: any = [];//类型表
    dealPerShow: boolean = false;
    waitSelUserList: any;
    outVal: any;
    userName: any;//当前登陆人
    orgName: any;//登陆人所属机构
    risk: boolean = false;   // 风险管控登记
    AshPlacing: boolean = true;  // 置灰
    custType: boolean = false;   //证件类型是否选择
    constructor(
        public httpService: CusListManageHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.userName = chName.userName
        this.orgName = chName.orgName
        this.userform = fb.group({
            isMybankCust: ['', Validators.required],//是否我行客户
            customerType: ['', Validators.required],//客户属性
            partyId: [''],//客户号
            custName: ['', Validators.required],//客户名称
            statusId: ['Y'],//状态
            listTypeOne: ['', Validators.required],//一级名单
            listTypeTwo: ['', Validators.required],//二级名单
            listTypeId: [''],//风险等级
            idType: ['', Validators.required],//证件类型
            idNo: ['', Validators.required],//证件号
            cellphone: [''],//联系号码
            contactAddress: ['', [Validators.maxLength(255), specialSymbol]],//联系地址
            inListType: ['1'],//录入方式
            classifyId: [''],//分类编码
            toListReason: [''],//录入原因
            registerUserName: [''],//登陆人
            registerOrgName: [""],//登陆机构
        })
    }

    @ViewChild('dialog') dialog: Dialog;
    ngOnInit() {
        console.log(this.user, "this.user")
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        console.log(chName, "chName")
        this.queryOrgName()
        this.codeValues() //调用方法，获取全部码值
        this.query();
        this.isMybankCustList = this.code['isMybankCust'];
        this.customerTypeList = this.code['customerType'];
        this.statusIdList = this.code['statusId'];
        this.inListTypeList = this.code['inListType'];
        this.idTypeList = this.code['CERT_TYPE'];
        this.isMybank();
        this.AshPlacing = true;
    }
    queryOrgName() {
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        for (var i = 0; i < chName.orgList.length; i++) {
            if (this.user.extParam.orgId == chName.orgList[i].orgId) {
                this.orgName = chName.orgList[i].orgName
            }
        }
        console.log(this.orgName, "111111111")
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //是否我行客户
    isMybank() {
        this.cust = false;
        if (this.userform.value.isMybankCust == 'Y' || this.userform.value.isMybankCust == '') {
            this.bank = true;
            this.bankown = false;
            this.userform.removeControl('idType');
            this.userform.removeControl('customerType');
            this.userform.removeControl('idNo');
            this.userform.removeControl('cellphone');
            this.userform.addControl('partyId', this.fb.control('', [Validators.required, Validators.maxLength(20), checkNumber]));
        } else if (this.userform.value.isMybankCust == 'N') {
            this.bank = false;
            this.bankown = true;
            this.userform.removeControl('partyId');
            this.userform.addControl('customerType', this.fb.control('', [Validators.required]));
            this.userform.addControl('idType', this.fb.control('', [Validators.required]));
            this.userform.addControl('cellphone', this.fb.control('', [Validators.maxLength(20), Validators.pattern(/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/)]));
            this.userform.addControl('idNo', this.fb.control('', [Validators.required, Validators.maxLength(32), checkEnAndNum]));
            this.userform.addControl('custName', this.fb.control('', [Validators.required]));
        }
    }
    // 个人和企业
    iscustomer() {
        debugger
        if (this.userform.value.isMybankCust == 'N' && this.userform.value.customerType == '2') { //企业
            this.cust = true;
            this.userform.removeControl('partyId');
            this.userform.removeControl('idType');
            this.userform.removeControl('idNo');
            this.userform.removeControl('custName');
            this.userform.addControl('idType', this.fb.control(''));
            this.userform.addControl('idNo', this.fb.control('', [Validators.maxLength(32), checkEnAndNum]));
            this.userform.addControl('customerType', this.fb.control('', [Validators.required]));
            this.userform.addControl('custName', this.fb.control('', [Validators.required]));
            this.userform.addControl('cellphone', this.fb.control('', [Validators.maxLength(20), Validators.pattern(/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/)]));
        }
        if (this.userform.value.isMybankCust == 'N' && this.userform.value.customerType == '1') {
            this.cust = false;
            this.userform.removeControl('partyId');
            this.userform.removeControl('custName');
            this.userform.removeControl('idType');
            this.userform.removeControl('idNo');
            this.userform.addControl('custName', this.fb.control(''));
            this.userform.addControl('customerType', this.fb.control('', [Validators.required]));
            this.userform.addControl('idType', this.fb.control('', [Validators.required]));
            this.userform.addControl('cellphone', this.fb.control('', [Validators.maxLength(20), Validators.pattern(/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/)]));
            this.userform.addControl('idNo', this.fb.control('', [Validators.required, Validators.maxLength(32), checkEnAndNum]));
        }
    }
    // 证件类型 是否身份证或其他
    idTypeChange(event) {
        console.log(event);
        if (this.userform.value.isMybankCust == 'N' && event.value && event.value == '0') {
            if (this.cust) {
                this.custType = true;
            }
            this.userform.removeControl('idNo');
            this.userform.addControl('idNo', this.fb.control('', [Validators.required, Validators.maxLength(18), checkEnAndNum]));
        } else if (this.userform.value.isMybankCust == 'N' && event.value && event.value != '0' && this.userform.value.customerType == '1') {
            this.userform.removeControl('idNo');
            this.userform.addControl('idNo', this.fb.control('', [Validators.required]));
        } else if (this.userform.value.isMybankCust == 'N' && event.value && event.value != '0' && this.userform.value.customerType == '2') {
            this.custType = true;
            this.userform.removeControl('idNo');
            this.userform.addControl('idNo', this.fb.control('', [Validators.required]));
        }
        if (!event.value) {
            this.custType = false;
            this.userform.removeControl('idNo');
            this.userform.addControl('idNo', this.fb.control(''));
        }
    }
    //名单联动
    listType(i) {
        if (i == '1') {
            const param = {};
            param['listTypeHierarchy'] = 2;
            param['parentListTypeId'] = this.userform.value.listTypeOne;
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let list = data.returnList;
                    if (data.returnList.length == 0) {
                        this.risk = true;
                        this.jbxx = [];
                        this.userform.get('classifyId').setValue("");
                    } else {
                        this.risk = false;
                        let list = data.returnList;
                        this.jbxx = [];
                        list.forEach(element => {
                            let obj = {};
                            obj['label'] = element.listTypeName;
                            obj['value'] = element.listTypeId;
                            this.jbxx.push(obj);
                        });
                    }
                    this.jbxx.unshift({ label: '请选择', value: '' });
                    this.listTypeTwoList = this.jbxx;
                    this.userform.get('listTypeTwo').setValue('');
                    this.userform.get('listTypeTwo').updateValueAndValidity();
                    this.msgs = [];
                    this.listTypeIdList = [];  //清空风险管控等级
                    // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else if (i == '2') {
            const param = {};
            param['listTypeHierarchy'] = 3;
            param['parentListTypeId'] = this.userform.value.listTypeTwo;
            this.httpService.selectBlackListType(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.reList = data.returnList;
                    if (data.returnList.length == 0) {
                        this.risk = true;
                        this.jbxx = [];
                        this.userform.get('classifyId').setValue("");
                        this.userform.get('classifyId').updateValueAndValidity();
                        this.userform.get('listTypeId').clearValidators();
                        this.userform.get('listTypeId').updateValueAndValidity();
                    } else {
                        this.risk = false;
                        let list = data.returnList;
                        this.jbxx = [];
                        list.forEach(element => {
                            let obj = {};
                            obj['label'] = element.listTypeName;
                            obj['value'] = element.listTypeId;
                            this.jbxx.push(obj);
                        });
                        this.userform.get('listTypeId').setValue('');
                        this.userform.get('listTypeId').setValidators([Validators.required]);
                        this.userform.get('listTypeId').updateValueAndValidity();
                    }
                    this.jbxx.unshift({ label: '请选择', value: '' });
                    this.listTypeIdList = this.jbxx;
                    this.msgs = [];
                    // this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else if (i == '3') {
            for (let e = 0; e < this.reList.length; e++) {
                if (this.userform.value.listTypeId == this.reList[e].listTypeId) {
                    this.userform.get('classifyId').setValue(this.reList[e].classifyId);
                }
            }
        }
    }
    //查询
    query() {
        const param = {};
        param['listTypeHierarchy'] = 1;
        this.httpService.selectBlackListType(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let list = data.returnList;
                list.forEach(element => {
                    let obj = {};
                    obj['label'] = element.listTypeName;
                    obj['value'] = element.listTypeId;
                    this.jbxx.push(obj);
                });
                this.jbxx.unshift({ label: '请选择', value: '' });
                this.listTypeOneList = this.jbxx;
                this.msgs = [];
                //  this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 保存/提交
    doSave(i) {
        if (i == '1') {
            this.msgs = [];
            const param = Object.assign({}, this.userform.value);
            param['registerOrg'] = this.user.extParam.orgId;
            param['registerUser'] = this.user.userId;
            param['registerUserName'] = this.userName
            param['registerOrgName'] = this.orgName
            param['approvalStatus'] = 'AA';
            param['operationType'] = '1001';
            param['registerTime'] = this.commfunc.transDateN(this.today);
            if (!this.userform.value.listTypeId && !this.risk) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请选择风险管控等级!' }];
                return;
            } else if (this.risk) {
                param['listTypeId'] = ''
            } else {
                for (let e = 0; e < this.reList.length; e++) {
                    if (this.userform.value.listTypeId == this.reList[e].listTypeId) {
                        param['listTypeId'] = this.reList[e].listTypeId;
                    }
                }
            }
            this.AshPlacing = false;
            //console.log(param,"1111111111111")
            this.httpService.addListTaskInfo(param).subscribe(data => {
                this.AshPlacing = true;                
                if (data.returnCode.code == 'success') {
                    this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                    setTimeout(() => {
                        this.outValue.emit(false)
                    }, 1000)
                } else {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.AshPlacing = true;
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else if (i == '2') {
            this.waitSelUserList = this.commfunc.query('KHYY_MDJSP', {});
            if (!this.waitSelUserList.erro) {
                this.outVal = this.waitSelUserList.waitSelUserList;
                let perPageShow = this.waitSelUserList.display;
                if (perPageShow == true) {
                    this.dealPerShow = true;
                } else {
                    const param = Object.assign({}, this.userform.value);
                    param['registerOrg'] = this.user.extParam.orgId;
                    param['registerUser'] = this.user.userId;
                    param['registerUserName'] = this.userName
                    param['registerOrgName'] = this.orgName
                    param['approvalStatus'] = 'AB';
                    param['operationType'] = '1001';
                    param['registerTime'] = this.commfunc.transDateN(this.today);
                    for (let e = 0; e < this.reList.length; e++) {
                        if (this.userform.value.listTypeId == this.reList[e].listTypeId) {
                            param['listTypeId'] = this.reList[e].listTypeId;
                        }
                    }
                    this.AshPlacing = false;
                    this.httpService.addListTaskInfo(param).subscribe(data => {
                        this.AshPlacing = true;                        
                        if (data.returnCode.code == 'success') {
                            this.msgs = [];
                            this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                            setTimeout(() => {
                                this.outValue.emit(false)
                            }, 1000)
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                        }
                    }, err => {
                        this.AshPlacing = true;
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                    });
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
            }
        }
    }
    outDisplayCall(event) {
        // this.dealPerShow = event;
        setTimeout(() => {
            this.dealPerShow = event;
        }, 4000)
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';
        let outFlowId;
        this.outVal.forEach(item => {
            ss += item.tellerId;
            specifyNextNode = item.userBank;
            outFlowId = item.outFlowId;
        });

        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = outFlowId;

        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        const param = Object.assign({}, this.userform.value);
        param['registerOrg'] = this.user.extParam.orgId;
        param['registerUser'] = this.user.userId;
        param['registerUserName'] = this.userName
        param['registerOrgName'] = this.orgName
        param['approvalStatus'] = 'AB';
        param['operationType'] = '1001';
        param['registerTime'] = this.commfunc.transDateN(this.today);
        param['authUser'] = ss;
        param['author'] = specifyNextNode;
        for (let e = 0; e < this.reList.length; e++) {
            if (this.userform.value.listTypeId == this.reList[e].listTypeId) {
                param['listTypeId'] = this.reList[e].listTypeId;
            }
        }
        this.httpService.addListTaskInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.outValue.emit(false);
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                setTimeout(() => {
                    this.outValue.emit(false)
                }, 1000)
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }

            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
                delete s.extParam.specifyNextOpers;
                delete s.extParam.specifyNextNode;
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }

        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 返回
    doReset() {
        this.outValue.emit(false);
    }
    // 客户查询弹框
    perTreeShow() {
        this.display = true;
    }
    // 客户查询关闭传值
    getMsg(e) {
        this.userform.get('partyId').setValue(e.custNo);
        this.userform.get('custName').setValue(e.custName);
        this.display = false;
    }

    ngAfterViewInit() {
        this.cancelDialogShake();
    }
    cancelDialogShake() {
        if (this.dialog) {
            this.dialog.center = function () {
                let elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                let elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                if (elementWidth === 0 && elementHeight === 0) {
                    this.containerViewChild.nativeElement.style.visibility = 'hidden';
                    this.containerViewChild.nativeElement.style.display = 'block';
                    elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                    elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                    this.containerViewChild.nativeElement.style.display = 'none';
                    this.containerViewChild.nativeElement.style.visibility = 'visible';
                }
                let viewport = this.domHandler.getViewport();
                let x = Math.max(Math.ceil((viewport.width - elementWidth)) / 2, 0);
                let y = Math.max(Math.ceil((viewport.height - elementHeight)) / 2, 0);
                // 解决抖动问题
                if (!this.containerViewChild.nativeElement.style.left ||
                    !this.containerViewChild.nativeElement.style.top ||
                    Math.abs(this.containerViewChild.nativeElement.style.left - x) > 5 ||
                    Math.abs(this.containerViewChild.nativeElement.style.top - y) > 5
                ) {
                    this.containerViewChild.nativeElement.style.left = x + 'px';
                    this.containerViewChild.nativeElement.style.top = y + 'px';
                }
            };
        }

    }
}