import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomListAnalyComponent } from './custom-list-analy/custom-list-analy.component';
import { CustomListLogComponent } from './custom-list-log/custom-list-log.component';
import { CustomStyleManageComponent } from './custom-style-manage/custom-style-manage.component';
import { CustomListManageComponent } from "app/pages/tzb/custom/custom-list-manage/custom-list-manage.component";
import { CustomBlackListComponent } from "app/pages/tzb/custom/custom-list-manage/custom-black-list/custom-black-list.component";
import { CustomMissionListComponent } from "app/pages/tzb/custom/custom-list-manage/custom-mission-list/custom-mission-list.component";
import { CustomElementManage } from "app/pages/tzb/custom/custom-list-manage/custom-element-manage/custom-element-manage.component";
import { customManage } from "app/pages/tzb/custom/custom-list-manage/custom-manage/custom-manage.component";
import { CustomBlackDetail } from "app/pages/tzb/custom/custom-list-manage/custom-black-list/custom-black-detail/custom-black-detail.component";
import { CustomMissionUpdate } from 'app/pages/tzb/custom/custom-list-manage/custom-mission-list/custom-mission-update/custom-mission-update.component';
export const routes: Routes = [
    {
        path: '',
        component: CustomListManageComponent,
        children: [
            { path: 'custom-black-list', component: CustomBlackListComponent },
            { path: 'custom-mission-list', component: CustomMissionListComponent },
            { path: 'custom-list-analy', component: CustomListAnalyComponent },
            { path: 'custom-list-log', component: CustomListLogComponent },
            { path: 'custom-style-manage', component: CustomStyleManageComponent },
            { path: 'custom-manage', component: customManage },
            { path: 'CustomBlackDetail', component: CustomBlackDetail },
        ]
    },
    { path: 'custom-element-manage', component: CustomElementManage },
    { path: 'CustomMissionUpdate', component: CustomMissionUpdate },
]
export const CustomListManageRouting: ModuleWithProviders = RouterModule.forChild(routes);