import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
    selector: 'custom-list-manage',
    templateUrl: './custom-list-manage.component.html',
    styleUrls: ['./custom-list-manage.component.scss']
})
export class CustomListManageComponent implements OnInit {
    show: number = 1;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
    ) {
    }
    ngOnInit() {
        let stu_1 = 1; // 名单调整申请
        let stu_2 = 2; // 名单调整历史
        let stu_3 = 3; // 名单类型维护
        let stu_4 = 4; // 名单统计分析
        let stu_5 = 5; // 名单操作日志
        let stu_6 = this.commfunc.permissionCheck('SID00053_P001_P001');// 超管
        if (stu_1) {
            this.show = 1;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-black-list'])
        } else if (stu_2) {
            this.show = 2;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-mission-list'])
        } else if (stu_3) {
            this.show = 3;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-style-manage'])
        } else if (stu_4) {
            this.show = 4;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-list-analy'])
        } else if (stu_5) {
            this.show = 5;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-list-log'])
        } else if (stu_6) {
            this.show = 6;
            this.router.navigate(['/pages/tzb/custom/custom-list-manage/custom-manage'])
        }
    }
    ngOnChanges() {
    }
    // 切换标签
    allot(num) {
        this.show = num;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}